<!--
 * @Autor: linxu
 * @Date: 2022-01-25 13:54:58
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-06 11:37:28
 * @Description: 一键报修记录
-->
<template>
  <div v-if="pageFlag === '1'">
    <el-container>
      <el-main>
        <div class="genset-oneclick-repair-record">
          <genset-info
            ref="gensetInfo"
            @handleRefresh="handleRefresh"></genset-info>
          <!-- 表格 -->
          <TableList
            height="100%"
            :options="options"
            :columns="columns"
            :dataSource="dataSource"
            :operates="operates(this)"
            :dataTotal="dataTotal"
            :pagination="paginationCfg"
            @handleChangePage="handleChangePage"
            @handleChangeSize="handleChangeSize"
            border
            v-loading="loading"
          />
        </div>
      </el-main>
      <toggle-crew></toggle-crew>
    </el-container>
  </div>
  <div v-else>
    <BMainTopComponent
      :isShowBackBtn="isShowBackBtn"
      :isHandleBackClick="true"
      @handleBackClick="handleBackClick"
    />
   
    <CurveReport :curveType="type"></CurveReport>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'
import ToggleCrew from '@/components/toggleCrew/list'
import CurveReport from '../components/CurveReport'
import GensetInfo from '@/views/MouldPageGenset/components/GensetInfo'
import moment from 'moment'
// 表格头设置
const columns = (that) => [
  {
    label: '索引值',
    prop: 'IdX',
    minWidth: '25%',
    formatter: (row, column, index) => {
      return `${row.IdX}`
    }
  },
  {
    label: '试验类型',
    prop: 'Trigger',
    minWidth: '25%'
  },
  {
    label: '创建时间',
    prop: 'Time',
    minWidth: '50%',
    formatter: (row, column, index) => {
      return row.Time ? that.$moment(row.Time).format('yyyy-MM-DD HH:mm:ss') : ''
    }
  }
]
// 表格操作按钮
const operates = (that) => [
  {
    label: '查看曲线',
    method: (row) => {
      that.handleShowCurve(row)
    }
  },
  {
    label: '删除',
    method: (row) => {
      that.handleDelete(row)
    },
    setStyle: (row) => {
      return 'color: red;'
    }
  },
  // {
  //   label: '重新请求',
  //   method: (row) => {
  //     that.handleReRequest(row)
  //   }
  // }
]
export default {
  name: 'GensetOneclickRepairRecord',
  mixins: [paginationConfigMixin],
  components: { 
    CurveReport, 
    ToggleCrew,
    GensetInfo
  },
  data() {
    return {
      isShowBackBtn: !!(this.$route.query.pageFlag === '0'),
      pageFlag: this.$route.query.pageFlag || '1', // 1 列表页 0 曲线页
      type: '1',
      // 表格
      columns: columns(this),
      operates,
      dataSource: [],
      options: {
        // height: '100%',
        index: true,
        labelIndex: '序号',
        props: {
          width: 248
        }
      },
      data: [],
      loading: false
    }
  },
  watch: {
    $route: {
      handler: function (newVal, oldVal) {
        console.log('$route', newVal)
        this.pageFlag = this.$route.query.pageFlag || '1'
        if (this.$route.query.pageFlag === '0') {
          this.isShowBackBtn = true
        } else {
          this.isShowBackBtn = false
        }
      },
      // 深度观察监听
      deep: true
    },
    selectCrew: {
      handler: function (nv) {
        this.paginationCfg.currentPage = 1
        this.queryTableData()
      }
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    handleRefresh() {
      this.$refs.gensetInfo.getGeneratorSet()
      this.queryTableData()
    },
    queryTableData() {
      this.loading = true
      const query = {
        page: this.paginationCfg.currentPage,
        pageSize: this.paginationCfg.pageSize
      }
      const params = {
        Type: '测试记录',
        Trigger: '一键报修',
        GeneratorId: this.selectCrew.ID
      }
      this.$api.equipment
        .GeneratorSetRecordingList(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            this.$set(this, 'dataSource', res.Data.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    // 查看曲线
    handleShowCurve(row) {
      // console.log('handleShowCurve', row)
      // this.pageFlag = 0
      const record = {
        IndexID: row.IdX,
        Type: row.Type,
        Trigger: row.Trigger,
        GeneratorSetId: row.GeneratorSetId
      }
      let text = this.$router.resolve({
        path: '/equipment/data-record/oneclick-repairrecord',
        query: { pageFlag: 0, record: JSON.stringify(record) }
      })
      window.open(text.href, '_blank')
    },
    handleBackClick() {
      // this.pageFlag = 1
      this.$router.push({
        path: '/equipment/data-record/oneclick-repairrecord',
        query: { pageFlag: 1 }
      })
    },
    // 删除
    handleDelete(row) {
      // console.log(row)
      const h = this.$createElement
      this.$confirm('删除提示', {
        title: '删除提示',
        message: h('div', [
          h('p', '此操作将导致数据无法恢复，请谨慎操作!'),
          h('p', '是否确定删除数据?')
        ]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          let query = {
            id: row.RecordingId
          }
          this.$api.equipment
            .DeleteGeneratorSetFullRecord(query)
            .then((res) => {
              if (res.Code === 1) {
                this.$message({
                  type: 'success',
                  message: '删除成功!'
                })
                this.paginationCfg.currentPage = 1
                this.queryTableData()
              }
            })
            .catch(() => {
              this.$message({
                type: 'success',
                message: '删除失败!'
              })
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 重新请求
    handleReRequest(row) {
      // console.log(row)
      this.$confirm('是否需要重新获取数据？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '重新获取数据成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    }
  }
}
</script>

<style lang="less" scoped>
.genset-oneclick-repair-record {
  display: inherit !important;
}
</style>



